<template>
  <div id="home" class="d-flex flex-column align-items-center">
    <div v-if="status == 0" id="startText" class="text-center rounded-circle my-5">ÂÆåÊàêÂæàËæõËã¶Ôºå<br>‰∏ÄÊ≠•‰∏ÄÊ≠•‰æÜ‰πüÂæàËæõËã¶Ôºõ<br>Ëã•‰∏çÂÅöÔºåÊ∞∏ÈÅ†ËæõËã¶üòÑ<br>ÈñãÂßãÂêßÔºÅ</div>
    <!-- ÈñãÂßã‰∏≠ -->
    <img v-else-if="!isBreak && status == 1" class="status-pic" src="../../public/img/user/dino1.gif">
    <!-- ‰ºëÊÅØ‰∏Ä‰∏ã -->
    <img v-else-if="isBreak" class="status-pic" src="../../public/img/user/break.svg">
    <!-- Êö´ÂÅú -->
    <img v-else class="status-pic" src="../../public/img/user/pause.svg">

    <p id="conetntText">{{ currentText }}</p>
    <p id="numText">{{ timetext }}</p>

    <div id="count-btn">
      <!-- icon ÈñãÂßã -->
      <b-btn class="count-btn" variant="outline-light" v-if="status !== 1" @click="start">GO
        <font-awesome-icon :icon="['fas','play']"></font-awesome-icon>
      </b-btn>
      <!-- icon Êö´ÂÅú -->
      <b-btn class="count-btn" variant="outline-light" v-if="status == 1" @click="pause">Êö´ÂÅú
        <font-awesome-icon :icon="['fas','pause']"></font-awesome-icon>
      </b-btn>
      <!-- icon Ë∑≥ÈÅé -->
      <b-btn class="count-btn" variant="outline-light" v-if="current.length > 0 || todos.length > 0" @click="finish(true)">Ë∑≥ÈÅé
        <font-awesome-icon :icon="['fas','step-forward']"></font-awesome-icon>
      </b-btn>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      // 0 = ÂÅúÊ≠¢
      // 1 = Êí≠Êîæ
      // 2 = Êö´ÂÅú
      status: 0,
      timer: 0
      // working,
      // breakTime
    }
  },
  computed: {
    currentText () {
      // Ê∏ÖÂñÆ‰∏≠ÊúâÊù±Ë•ø -> Â∞±ÊúÉÂá∫Áèæ'ÈªûÊìäÈñãÂßã'ÔºåÂ¶ÇÊûúÊ≤íÊù±Ë•ø -> Â∞±ÊòØ'Ê≤íÊúâ‰∫ãÈ†Ö'
      return this.current.length > 0 ? this.current : this.todos.length > 0 ? 'ÈªûÊìäÈñãÂßã' : 'Ë´ãÂÖàË®≠ÂÆö‰∫ãÈ†Ö'
    },
    timetext () {
      // this.timeleft Âú®Áí∞Â¢ÉÊ™îË£° ÁÇ∫ 5
      const m = Math.floor(this.timeleft / 60)
      const s = Math.floor(this.timeleft % 60)
      return `${m}Ôºö${s}`
    },
    alarm () {
      return this.$store.getters.alarm
    },
    timeleft () {
      return this.$store.getters.timeleft
    },
    current () {
      return this.$store.getters.current
    },
    todos () {
      return this.$store.getters.todos
    },
    isBreak () {
      return this.$store.getters.isBreak
    }
  },
  methods: {
    start () {
      // Êö´ÂÅúÂæåÁπºÁ∫å
      if (this.status === 2) {
        this.status = 1
        this.timer = setInterval(() => {
          this.$store.commit('countdown')
          if (this.timeleft <= 0) {
            this.finish(false)
          }
        }, 1000)
      } else {
      // ÈñãÂßãÊñ∞ÂÄíÊï∏
        if (this.todos.length > 0) {
          this.$store.commit('start')
          this.status = 1
          this.timer = setInterval(() => {
            this.$store.commit('countdown')
            if (this.timeleft <= 0) {
              this.finish()
            }
          }, 1000)
        }
      }
    },
    finish (skip) {
      clearInterval(this.timer)
      this.status = 0
      this.$store.commit('finish')

      if (!skip) {
        const audio = new Audio()
        audio.src = './alarms/' + this.alarm
        audio.play()
      }

      if (this.todos.length > 0) {
        this.start()
      } else {
        setTimeout(() => {
          alert('ÁµêÊùü')
        }, 10)
      }
    },
    pause () {
      clearInterval(this.timer)
      this.status = 2
    }
  }
}
</script>
